import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { InvoiceComponent } from './invoice.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';

const routes: Routes = [
  {
    path: '',
    component:  InvoiceComponent
  },
  {
    path: 'order-invoice',
    loadChildren: () => import('./order-invoice/order-invoice.module').then(m => m.OrderInvoiceModule)
  },
  {
    path: 'ingredient',
    loadChildren: () => import('./ingredient/ingredient.module').then(m => m.IngredientModule)
  },
  {
    path: 'import-kitchen',
    loadChildren: () => import('./import-kitchen/import-kitchen.module').then(m => m.ImportKitchenModule)
  },
  {
    path: 'purchase',
    loadChildren: () => import('./purchase/purchase.module').then(m => m.PurchaseModule)
  },
  {
    path: 'export-kitchen',
    loadChildren: () => import('./export-kitchen/export-kitchen.module').then(m => m.ExportKitchenModule)
  },
  {
    path: 'return-supplier',
    loadChildren: () => import('./return-supplier/return-supplier.module').then(m => m.ReturnSupplierModule)
  },
  {
    path: 'opening-stock',
    loadChildren: () => import('./opening-stock/opening-stock.module').then(m => m.OpeningStockModule)
  },
  {
    path: 'temporary-order-invoice',
    loadChildren: () => import('./temporary-order-invoice/temporary-order-invoice.module').then(m => m.TemporaryOrderInvoiceModule)
  },
  {
    path: 'temporary-purchase-invoice',
    loadChildren: () => import('./temporary-purchase-invoice/temporary-purchase-invoice.module').then(m => m.TemporaryPurchaseInvoiceModule)
  },
  {
    path: 'destroy-product',
    loadChildren: () => import('./destroy-product/destroy-product.module').then(m => m.DestroyProductModule)
  },
  {
    path: 'e-invoice',
    loadChildren: () => import('./e-invoice/e-invoice.module').then(m => m.EInvoiceModule)
  },
]

@NgModule({
  declarations: [InvoiceComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule
  ]
})
export class InvoiceModule { }
